or-scheme: light;\n  }\n\n  background-image: var(--img-url);\n  background-size: 100% 100%;\n  background-repeat: no-repeat;\n  background-color: rgb(var(--color-bg));\n  color: rgb(var(--color-text));\n  display: grid;\n  grid-template-rows: 25% 1fr 15%;\n  min-height: 100dvh;\n  overflow-x: hidden;\n  padding: 0 10%;\n  width: 100%;\n}\n\n// THEME BUTTON\n.header {\n  align-items: center;\n  display: flex;\n  justify-content: space-between;\n  width: 100%;\n\n  &__subject {\n    align-items: center;\n    display: flex;\n    gap: 3.2rem;\n\n    img {\n      background-color: rgba(var(--color-quiz--icon), 0.05);\n      border-radius: 8px;\n      padding: 0.8rem;\n    }\n\n    p {\n      font-size: 2.8rem;\n    }\n  }\n}\n\n.theme {\n  align-items: center;\n  display: flex;\n  gap: 1.6rem;\n\n  &__icon--sun {\n    background-image: var(--img-icon--sun);\n    background-repeat: no-repeat;\n    height: 2.4rem;\n    width: 2.4rem;\n  }\n\n  &__icon--moon {\n    background-image: var(--img-icon--moon);\n    background-repeat: no-repeat;\n    height: 2.4rem;\n    width: 2.4rem;\n  }\n\n  &__btn {\n    background-color: rgb(var(--color-option));\n    cursor: pointer;\n    height: 2.8rem;\n    position: relative;\n    width: 4.8rem;\n    border-radius: 16px;\n  }\n  &__btn::before {\n    background-color: white;\n    border-radius: 100%;\n    content: \"\";\n    height: 2rem;\n    position: absolute;\n    top: 50%;\n    transform: translateY(-50%);\n    transition: left 0.3s ease, right 0.3s ease;\n    width: 2rem;\n  }\n}\n\n.dark .theme__btn::before {\n  right: 3px;\n}\n\n.light .theme__btn::before {\n  left: 3px;\n}\n//\n\n// MAIN SUBJECTS\n.container {\n  display: none;\n  &.show {\n    color: rgb(var(--color-text));\n    display: grid;\n    gap: 3.2rem;\n    grid-template-columns: 1fr 1fr;\n    justify-content: center;\n  }\n}\n.home {\n  display: flex;\n  flex-direction: column;\n  gap: 4rem;\n\n  h1 {\n    font-size: 6.4rem;\n  }\n\n  p {\n    color: rgb(var(--color-text--secondary));\n    font-size: 2rem;\n  }\n}\n\n.subjects {\n  display: flex;\n  flex-direction: column;\n  gap: 3.2rem;\n\n  &__subject {\n    align-items: center;\n    background-color: rgb(var(--color-quiz));\n    border-radius: 8px;\n    box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.1);\n    border: 3px solid transparent;\n    cursor: pointer;\n    display: flex;\n    gap: 3.2rem;\n    padding: 1.2rem;\n\n    &:hover {\n      border: 3px solid rgba(var(--color-option), 0.5);\n      color: rgba(var(--color-option), 0.5);\n      transition: all 0.3s ease;\n    }\n\n    &:focus,\n    &:active {\n      border: 3px solid rgba(var(--color-option), 1);\n      color: rgba(var(--color-option), 1);\n    }\n\n    img {\n      background-color: rgba(var(--color-quiz--icon), 0.05);\n      border-radius: 8px;\n      padding: 0.8rem;\n    }\n\n    p {\n      font-size: 2.8rem;\n    }\n  }\n}\n\n// MAIN QUIZ\n.quiz {\n  display: none;\n  &.show {\n    color: rgb(var(--color-text));\n    display: grid;\n    gap: 3.2rem;\n    grid-template-columns: 1fr 1fr;\n    justify-content: center;\n  }\n\n  &__home {\n    display: flex;\n    flex-direction: column;\n    gap: 4rem;\n\n    h2 {\n      font-size: 6.4rem;\n    }\n\n    p {\n      color: rgb(var(--color-text--secondary));\n      font-size: 2rem;\n    }\n  }\n\n  &__answers {\n    display: flex;\n    flex-direction: column;\n    gap: 3.2rem;\n  }\n\n  &__option {\n    background-color: rgba(var(--color-quiz--icon), 0.05);\n    border-radius: 8px;\n    font-weight: bold;\n    padding: 1.6rem;\n  }\n\n  &__answer {\n    align-items: center;\n    background-color: rgb(var(--color-quiz));\n    border-radius: 8px;\n    border: 1px solid transparent;\n    box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.1);\n    cursor: pointer;\n    display: flex;\n    gap: 3.2rem;\n    padding: 1.2rem;\n\n    &.active {\n      border: solid 1px rgb(var(--color-option));\n    }\n\n    &.error {\n      border: solid 1px rgb(var(--color-error));\n    }\n\n    &.correct {\n      border: solid 1px rgb(var(--color-correct));\n    }\n\n    &:not(.active):hover .quiz__option {\n      background-color: rgba(var(--color-option), 0.1);\n      color: rgb(var(--color-option));\n      transition: all 0.3s ease;\n    }\n\n    &.active .quiz__option {\n      background-color: rgba(var(--color-option), 1);\n      color: white;\n      transition: all 0.3s ease;\n    }\n    &.error .quiz__option {\n      background-color: rgb(var(--color-error));\n    }\n    &.correct .quiz__option {\n      background-color: rgb(var(--color-correct));\n    }\n\n    &.disabled {\n      pointer-events: none;\n    }\n\n    p {\n      font-size: 1.6rem;\n    }\n  }\n\n  &__error {\n    align-items: center;\n    display: flex;\n    gap: 3.2rem;\n    justify-content: center;\n\n    p {\n      color: rgb(var(--color-error));\n      font-size: 2.4rem;\n    }\n  }\n}\n\n// COMPLRETED QUIZ\n\n.result {\n  display: none;\n\n  &.show {\n    color: rgb(var(--color-text));\n    display: grid;\n    grid-template-columns: 1fr 1fr;\n  }\n\n  &__home {\n    display: flex;\n    flex-direction: column;\n    font-size: 6.4rem;\n    gap: 1.2rem;\n\n    h2 {\n      font-weight: bold;\n    }\n  }\n\n  &__title {\n    align-items: center;\n    display: flex;\n    gap: 3.2rem;\n\n    span {\n      font-size: 2.8rem;\n    }\n\n    img {\n      background-color: rgba(var(--color-quiz--icon), 0.05);\n      border-radius: 8px;\n      padding: 0.8rem;\n    }\n  }\n\n  &__info {\n    align-items: center;\n    display: flex;\n    flex-direction: column;\n    gap: 3.2rem;\n  }\n  &__box {\n    align-items: center;\n    border-radius: 8px;\n    box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.1);\n    background-color: rgb(var(--color-quiz));\n    display: flex;\n    flex-direction: column;\n    gap: 1.6rem;\n    padding: 3.2rem;\n    width: 100%;\n  }\n\n  &__length {\n    font-size: 2.4rem;\n  }\n\n  &__points {\n    font-size: 14.4rem;\n    font-weight: bold;\n  }\n}\n\n// BUTTON\n\n.